<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-dialog
      v-model="approveResultsDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Confirm
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="approveResultsDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-title>
          Are you sure you want to approve results?
        </v-card-title>
        <v-card-text>
          <v-menu
            v-model="registrationDateMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="registrationDateFormatted"
                label="Provide Registration Date*"
                prepend-icon="mdi-event"
                readonly
                v-on="on"
                outlined
                dense
              />
            </template>
            <v-date-picker
              v-model="registrationDate"
              @change="registrationDateFormatted = formatDate(registrationDate)"
              @input="registrationDateMenu = false"
            />
          </v-menu>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" :disabled="!registrationDate" @click="approveResults"><v-icon>mdi-check</v-icon>Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="applicants.dialog"
      persistent
      max-width="1000px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          <template v-if="applicantStatus === 'waitingexam'">
            Select Applicants To Enroll
          </template>
          <template v-if="applicantStatus === 'failed'">
            Enroll Retaking Candidates
          </template>
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="applicants.dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-title>
          <v-text-field label="Omang Number" class="ma-2" v-model="search.omang" dense outlined hide-details clearable @change="buildTerms('applicants')" @click:clear="clearSearch('omang', 'applicants')" />
          <v-text-field label="Passport Number" class="ma-2" v-model="search.passport" dense outlined hide-details clearable @change="buildTerms('applicants')" @click:clear="clearSearch('passport', 'applicants')" />
        </v-card-title>
        <v-card-subtitle
          v-if="applicants.error_message"
          class="white--text error"
        >{{ applicants.error_message }}</v-card-subtitle>
        <v-card-text>
          {{ applicants.selected.length }} Selected
          <v-data-table
            v-model="applicants.selected"
            style="cursor: pointer"
            :headers="applicants.headers"
            :items="applicants.results"
            item-key="id"
            :options.sync="applicants.options"
            :server-items-length="applicants.total"
            :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
            :loading="applicants.loadingApplicants"
            class="elevation-1"
            show-select
          >
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-btn color="success" :disabled="applicants.selected.length === 0" @click="enroll">Enroll Selected</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-card 
      class="mx-auto"
      max-width="900"
      outlined
      >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        NMCB Scheduled Exam
      </v-card-title>
      <v-card-text class="my-3">
        <v-row>
          <v-col cols="4">
            <v-btn :disabled="!canEnroll" color="secondary" dark small @click="getApplicants(true, 'waitingexam')">
              <v-icon light left>mdi-playlist-check</v-icon>
              <span>Enroll Candidates</span>
            </v-btn>
            <br>
            <br>
            <v-btn :disabled="!canEnroll" color="secondary" dark small @click="getApplicants(true, 'failed')">
              <v-icon light left>mdi-playlist-check</v-icon>
              <span>Enroll Retaking Candidates</span>
            </v-btn>
            <br>
            <br>
            <v-btn :disabled="exam && exam.status && exam.status.code === 'approved'" color="success" dark small @click="approveResultsDialog = true">
              <v-icon light left>mdi-check</v-icon>
              <span>Approve Resultss</span>
            </v-btn>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col cols="8">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Cadres Categories:</span>
              </v-col>
              <v-col cols="8">
                <v-chip
                  class="ma-2"
                  v-for="(cadre, ind) in exam.cadres"
                  :key="ind"
                >
                  {{ cadre.display }}
                </v-chip>
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Assessments:</span>
              </v-col>
              <v-col cols="8">
                <v-chip
                  class="ma-2"
                  v-for="(assessment, ind) in exam.assessments"
                  :key="ind"
                >
                  {{ assessment.display }}
                </v-chip>
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Pass Mark:</span>
              </v-col>
              <v-col cols="8">{{ exam.passmark }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Start Date:</span>
              </v-col>
              <v-col cols="8">{{ exam.startdate }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">End Date:</span>
              </v-col>
              <v-col cols="8">{{ exam.enddate }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Description:</span>
              </v-col>
              <v-col cols="8">{{ exam.desciption }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Status:</span>
              </v-col>
              <v-col cols="8">{{ exam.status.display }}</v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <br>
    <v-card>
      <v-card-title>
        Enrolled Applicants
        <v-spacer></v-spacer>
        <v-btn
          color="error"
          small
          :disabled="applicants.selected.length === 0"
          @click="unenroll"
        >
          <v-icon left>mdi-close-outline</v-icon>Un Enroll Selected
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn color="primary" v-if="!resultsMode" @click="activateAddResults">
          <template v-if="exam && exam.status && exam.status.code === 'approved'">
            View Results
          </template>
          <template v-else>
            Add Results
          </template>
        </v-btn>
        <template v-else>
          <v-btn color="success" :disabled="exam && exam.status && exam.status.code === 'approved'" @click="saveResults">Save Results</v-btn>
          &nbsp;&nbsp;&nbsp;
          <v-btn color="primary" @click="deactivateAddResults">Hide Results</v-btn>
        </template>
      </v-card-title>
      <v-card-title>
        <v-text-field label="Omang Number" class="ma-2" v-model="search.omang" dense outlined hide-details clearable @change="buildTerms('enrolled')" @click:clear="clearSearch('omang', 'enrolled')" />
        <v-text-field label="Passport Number" class="ma-2" v-model="search.passport" dense outlined hide-details clearable @change="buildTerms('enrolled')" @click:clear="clearSearch('passport', 'enrolled')" />
      </v-card-title>
      <v-card-subtitle
        v-if="enrolled.error_message"
        class="white--text error"
      >{{ enrolled.error_message }}</v-card-subtitle>
      <v-card-text>
        <v-data-table
          style="cursor: pointer"
          :headers="enrolled.headers"
          :items="enrolled.results"
          item-key="id"
          :options.sync="enrolled.options"
          :server-items-length="enrolled.total"
          :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
          :loading="enrolled.loadingEnrolled"
          class="elevation-1"
          show-select
          v-model="applicants.selected"
        >
          <template v-slot:item="{ item }">
            <tr @click="clickIt(item)">
              <td>
                <v-checkbox
                  @click.stop
                  v-model="applicants.selected"
                  color="red"
                  :value="item"
                  hide-details
                ></v-checkbox>
              </td>
              <td>{{ item.surname }}</td>
              <td>{{ item.given }}</td>
              <td>{{ item.birthDate }}</td>
              <td>{{ item.gender }}</td>
              <td>{{ item.omang[0] }}</td>
              <td>{{ item.passport[0] }}</td>
              <td>{{ item.cadreCategory }}</td>
              <td>{{ item.location }}</td>
              <template v-if="resultsMode">
                <td v-for="assessment in exam.assessments" :key="item.id+assessment.code">
                  <v-text-field
                    @click.stop
                    :readonly="exam && exam.status && exam.status.code === 'approved'"
                    :error-messages="examResults[item.examId][assessment.code].errors"
                    v-model="examResults[item.examId][assessment.code].score"
                    outlined
                    hide-details="auto"
                    dense
                    @input="validateScore(item.examId)"
                  />
                </td>
                <td>
                  <v-chip dark color="green" v-if="examResults[item.examId]['exam-assessment'] === 'Pass'">
                    {{ examResults[item.examId]["exam-assessment"] }}
                  </v-chip>
                  <v-chip dark color="red" v-if="examResults[item.examId]['exam-assessment'] === 'Fail'">
                    {{ examResults[item.examId]["exam-assessment"] }}
                  </v-chip>
                </td>
              </template>
            </tr>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      resultsMode: false,
      approveResultsDialog: false,
      registrationDate: "",
      registrationDateFormatted: "",
      registrationDateMenu: false,
      exam: {
        cadres: [],
        assessments: [],
        status: {}
      },
      examResults: {},
      search: {
        name: '',
        omang: '',
        passport: ''
      },
      enrolled: {
        options: { itemsPerPage: 5 },
        total: 0,
        prevPage: -1,
        link: [],
        terms: {},
        results: [],
        headers: [{
          text: "Surname",
          value: "surname"
        }, {
          text: "Given Name(s)",
          value: "given"
        }, {
          text: "Birth Date",
          value: "birthDate"
        }, {
          text: "Gender",
          value: "gender"
        }, {
          text: "OMANG",
          value: "omang"
        }, {
          text: "Passport",
          value: "passport"
        }, {
          text: "Cadre Category",
          value: "cadreCategory"
        }, {
          text: "Training Location",
          value: "location"
        }],
        loadingEnrolled: false,
        update_again: { rerun: false, restart: false },
        error_message: null,
      },
      applicants: {
        selected: [],
        dialog: false,
        options: { itemsPerPage: 2 },
        total: 0,
        prevPage: -1,
        link: [],
        terms: {},
        results: [],
        headers: [{
          text: "Surname",
          value: "surname"
        }, {
          text: "Given Name(s)",
          value: "given"
        }, {
          text: "Birth Date",
          value: "birthDate"
        }, {
          text: "Gender",
          value: "gender"
        }, {
          text: "OMANG",
          value: "omang"
        }, {
          text: "Passport",
          value: "passport"
        }, {
          text: "Cadre Category",
          value: "cadreCategory"
        }, {
          text: "Training Location",
          value: "location"
        }, {
          text: "Attempts",
          value: "attempt"
        }],
        loadingApplicants: false,
        update_again: { rerun: false, restart: false },
        error_message: null,
      },
      applicantStatus: ''
    }
  },
  watch: {
    'applicants.options': {
      handler() {
        this.getApplicants();
      },
      deep: true
    },
    'enrolled.options': {
      handler() {
        this.getEnrolled();
      },
      deep: true
    }
  },
  computed: {
    canEnroll() {
      if(this.exam && this.exam.status && this.exam.status.code === 'approved') {
        return false
      }
      if(this.$moment(this.exam.enddate).isBefore(this.$moment().format("YYYY-MM-DD"))) {
        return false
      }
      return true
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    selectAll() {
      this.applicants.selected = this.applicants.selected.concat(this.applicants.results)
    },
    unselectDisabled(item) {
      if(item.disabled) {
        let index = this.applicants.selected.findIndex((selelected) => {
          return selelected.id === item.id
        })
        if(index > -1) {
          this.applicants.selected.splice(index, 1)
        }
      }
    },
    approveResults() {
      this.approveResultsDialog = false
      this.overlay = true
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify({
          exam: this.queries.exam,
          registrationDate: this.registrationDate
        })
      }
      fetch("/site/nmcb/approveResults", opts).then((response) => {
        this.overlay = false;
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Results saved successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to generate registrations for some few applicants, try again."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to generate registrations for some few applicants, try again."
        });
      })
    },
    clickIt: function(record) {
      this.$router.push({
        path: "/custom/nmcb-registration-application?id=" + record.application + "&practitioner=" + record.id + "&path=/components/nmcb/"
      })
    },
    validateScore(id) {
      let total = 0
      let errors = false
      let complete = true
      for(let assessment of this.exam.assessments) {
        this.examResults[id][assessment.code].errors = []
        let score = this.examResults[id][assessment.code].score
        if(score && score.toString().split(".").length > 1 && !score.toString().split(".")[1]) {
          this.examResults[id][assessment.code].errors = ["Incomplete number"]
          errors = true
        }
        if(isNaN(score)) {
          this.examResults[id][assessment.code].errors = ["Must be a number"]
          errors = true
          break
        }
        if(score) {
          total += parseFloat(score)
        } else {
          complete = false
        }
        if(total > 100) {
          this.examResults[id][assessment.code].errors = ["Total Exceeds 100"]
          errors = true
        }
      }
      if(errors || !complete) {
        this.examResults[id]["exam-assessment"] = ""
      } else if(this.exam.passmark > total) {
        this.examResults[id]["exam-assessment"] = "Fail"
      } else if(this.exam.passmark <= total) {
        this.examResults[id]["exam-assessment"] = "Pass"
      }
    },
    activateAddResults() {
      this.resultsMode = true
      for(let assessment of this.exam.assessments) {
        this.enrolled.headers.push({
          text: assessment.display,
          value: assessment.code
        })
      }
      this.enrolled.headers.push({
        text: "Assessment",
        value: "exam-assessment"
      })
    },
    deactivateAddResults() {
      this.resultsMode = false
      for(let assessment of this.exam.assessments) {
        let index = this.enrolled.headers.findIndex((header) => {
          return header.value === assessment.code
        })
        this.enrolled.headers.splice(index, 1)
      }
      let index = this.enrolled.headers.findIndex((header) => {
        return header.value === 'exam-assessment'
      })
      this.enrolled.headers.splice(index, 1)
    },
    saveResults() {
      this.overlay = true
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(this.examResults)
      }
      fetch("/site/nmcb/saveResults", opts).then((response) => {
        this.overlay = false;
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Results saved successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to save results."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to save results."
        });
      })
    },
    enroll() {
      this.applicants.dialog = false
      this.overlay = true;
      let bundle = {
        resourceType: "Bundle",
        type: "transaction",
        entry: []
      }
      for(let applicant of this.applicants.selected) {
        let resource = {
          resourceType: "Basic",
          meta: {
            profile: ["http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile"]
          },
          extension: [{
            url: "http://ihris.org/fhir/StructureDefinition/exam-attempt",
            valueInteger: applicant.attempt + 1
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
            valueReference: {
              reference: "Practitioner/" + applicant.id
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/nmcb-registration-application-reference",
            valueReference: {
              reference: "Basic/" + applicant.application
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/nmcb-exam-scheduling-reference",
            valueReference: {
              reference: "Basic/" + this.queries.exam
            }
          }]
        }
        bundle.entry.push({
          resource,
          request: {
            method: "POST",
            url: "Basic"
          }
        })
      }
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(bundle)
      }
      fetch("/site/nmcb/enrollApplicants", opts).then((response) => {
        this.applicants.selected = []
        this.overlay = false;
        if(response.status === 201 || response.status === 200) {
          this.getEnrolled()
          this.$store.commit("setMessage", {
            type: "success",
            text: "Applicants Enrolled Successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to enroll applicants."
          });
        }
      }).catch(() => {
        this.applicants.selected = []
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to enroll applicants."
        });
      })
    },
    unenroll() {
      this.overlay = true;
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify({
          applicants: this.applicants.selected,
          exam: this.queries.exam
        })
      }
      fetch("/site/nmcb/unenrollApplicants", opts).then((response) => {
        this.overlay = false;
        this.applicants.selected = []
        if(response.status === 201 || response.status === 200) {
          this.getEnrolled()
          this.$store.commit("setMessage", {
            type: "success",
            text: "Applicants Un Enrolled Successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to un enroll applicants."
          });
        }
      }).catch(() => {
        this.applicants.selected = []
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to un enroll applicants."
        });
      })
    },
    getExam() {
      fetch("/fhir/Basic/" + this.queries.exam).then((response) => {
        response.json().then((exam) => {
          let cadres = exam.extension.filter((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-cadre-category'
          })
          this.exam.cadres = []
          for(let cadre of cadres) {
            this.exam.cadres.push({
              code: cadre.valueCoding.code,
              display: cadre.valueCoding.display
            })
          }
          let assessments = exam.extension.filter((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-exam-assessment-type'
          })
          this.exam.assessments = []
          for(let assessment of assessments) {
            this.exam.assessments.push({
              code: assessment.valueCoding.code,
              display: assessment.valueCoding.display
            })
          }
          this.exam.passmark = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-exam-passmark'
          }).valueDecimal
          this.exam.startdate = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/start-date'
          }).valueDate
          this.exam.enddate = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/end-date'
          }).valueDate
          let descr = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/comments'
          })
          if(descr) {
            this.exam.desciption = descr.valueString
          }
          let status = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-examresults-status'
          })
          if(status) {
            this.exam.status = status.valueCoding
          }
        })
      })
    },
    clearSearch: function(field, category) {
      this.search[field] = ""
      this[category].terms.practitioner = ''
      this.buildTerms(category)
    },
    checkRerunApplicants() {
      if ( !this.applicants.loadingApplicants && this.applicants.update_again.rerun ) {
        this.getApplicants( this.applicants.update_again.restart )
        this.applicants.update_again = { rerun: false, restart: false }
      }
    },
    checkRerunEnrolled() {
      if ( !this.enrolled.loadingEnrolled && this.enrolled.update_again.rerun ) {
        this.getEnrolled( this.enrolled.update_again.restart )
        this.enrolled.update_again = { rerun: false, restart: false }
      }
    },
    async buildTerms(category) {
      if(category === "applicants") {
        this.applicants.terms = {}
        await this.getDataPractitioner(category)
        this.getApplicants(true);
      } else if(category === "enrolled") {
        this.enrolled.terms = {}
        await this.getDataPractitioner(category)
        this.getEnrolled(true);
      }
    },
    getDataPractitioner(category) {
      return new Promise((resolve, reject) => {
        let identifier = ""
        if(this.search.omang) {
          identifier = "?identifier=" + this.search.omang
        }
        if(this.search.passport) {
          if(identifier) {
            identifier += "&identifier=" + this.search.passport
          } else {
            identifier = "?identifier=" + this.search.passport
          }
        }
        if(!identifier) {
          return resolve()
        }
        let url = "/fhir/Practitioner" + identifier
        fetch(url).then(response => {
          response.json().then(async (data) => {
            let practitioners = []
            if(data.entry && data.entry.length > 0) {
              for (let entry of data.entry) {
                practitioners.push("Practitioner/" + entry.resource.id)
              }
            }
            if(practitioners.length > 0) {
              this[category].terms["practitioner"] = practitioners.join(",")
            } else {
              this[category].terms["practitioner"] = "Practitioner/fakeprct"
            }
            resolve()
          }).catch(() => {
            reject()
          })
        }).catch(() => {
          reject()
        })
      })
    },
    getApplicants(restart, status) {
      if(status) {
        this.applicantStatus = status
      }
      if(!this.applicants.dialog) {
        this.applicants.selected = []
      }
      this.applicants.dialog = true
      if ( this.applicants.loadingApplicants ) {
        this.applicants.update_again.rerun = true
        this.applicants.update_again.restart = this.applicants.update_again.restart || restart
        return
      }
      this.applicants.loadingApplicants = true;
      this.applicants.error_message = null;
      let cadres = ""
      for(let cadre of this.exam.cadres) {
        if(!cadres) {
          cadres = cadre.code
        } else {
          cadres += "," + cadre.code
        }
      }
      let url = "";
      if (restart) this.applicants.options.page = 1;
      if (this.applicants.options.page > 1) {
        if (this.applicants.options.page === this.applicants.prevPage - 1) {
          url = this.applicants.link.find(link => link.relation === "previous").url;
        } else if (this.applicants.options.page === this.applicants.prevPage + 1) {
          url = this.applicants.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace(/_getpages=[^&]*&*/, "").replace("/fhir?","/fhir/Basic?")
        url = url.substring(url.indexOf("/fhir/"));

        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_profile=http://ihris.org/fhir/StructureDefinition/nmcb-registration-application-profile') === -1) {
          url = url + '&_profile=http://ihris.org/fhir/StructureDefinition/nmcb-registration-application-profile'
        }
        if(url.indexOf('nmcbapplicationstatus=' + this.applicantStatus) === -1) {
          url = url + '&nmcbapplicationstatus=' + this.applicantStatus
        }
        if(url.indexOf('nmcbcadrecategory:includes=' + cadres) === -1) {
          url = url + '&nmcbcadrecategory:includes=' + cadres
        }
        if(url.indexOf('_revinclude=Basic:nmcbapplication') === -1) {
          url = url + '&_revinclude=Basic:nmcbapplication'
        }
        if(url.indexOf('_include=Basic:practitioner') === -1) {
          url = url + '&_include=Basic:practitioner'
        }
        if(url.indexOf('_sort=-_lastUpdated') === -1) {
          url = url + '&_sort=-_lastUpdated'
        }
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
      }
      if (url === "") {
        let count = this.applicants.options.itemsPerPage;
        let sort = "";
        for (let idx in this.applicants.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.applicants.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.applicants.options.sortBy[idx];
        }
        url =
          "/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/nmcb-registration-application-profile" + 
          "&nmcbapplicationstatus=" + this.applicantStatus +
          "&nmcbcadrecategory:includes=" + cadres + 
          "&_revinclude=Basic:nmcbapplication" + 
          "&_include=Basic:practitioner" + 
          "&_count=" + count +
          "&_sort=-_lastUpdated&_total=accurate"
      }
      let sTerms = Object.keys(this.applicants.terms);
      for (let term of sTerms) {
        if ( Array.isArray( this.applicants.terms[term] ) ) {
          if ( this.applicants.terms[term].length > 0 ) {
            url += "&" + term + "=" + this.applicants.terms[term].join(',')
          }
        } else if ( this.applicants.terms[term] ) {
          url += "&" + term + "=" + this.applicants.terms[term];
        }
      }

      this.applicants.prevPage = this.applicants.options.page;
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.applicants.results = [];
          if (data.total > 0) {
            this.applicants.link = data.link;
            for (let entry of data.entry) {
              if(entry.resource.resourceType !== 'Practitioner') {
                continue
              }
              let result = { id: entry.resource.id };
              result.surname = entry.resource.name[0].family
              result.given = entry.resource.name[0].given.join(", ")
              result.birthDate = entry.resource.birthDate
              result.gender = entry.resource.gender
              result.gender = await this.$fhirutils.lookup( result.gender, 'http://hl7.org/fhir/administrative-gender' )
              result.omang = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='omang').value" );
              result.passport = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='passport').value" );
              let enrolledExams = data.entry.filter((entry) => {
                return entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile") &&
                entry.resource.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference" &&ext.valueReference.reference === "Practitioner/" + result.id
                })
              })
              if(enrolledExams) {
                let enrolled
                for(let enrolledExam of enrolledExams) {
                  enrolled = enrolledExam.resource.extension.find((ext) => {
                    return ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-exam-scheduling-reference" && ext.valueReference.reference === "Basic/" + this.queries.exam
                  })
                  if(enrolled) {
                    break
                  }
                  let attempt = enrolledExam.resource.extension.find((ext) => {
                    return ext.url === "http://ihris.org/fhir/StructureDefinition/exam-attempt"
                  })
                  if(attempt && (!result.attempt || result.attempt < attempt.valueInteger)) {
                    result.attempt = attempt.valueInteger
                  }
                }
                if(enrolled) {
                  continue
                }
                if(result.attempt >= 5) {
                  continue
                }
                if(!result.attempt) {
                  result.attempt = 0
                }
              }
              // this ensure that there is no double enrollment resulting from double scheduling
              let alreadyEnrolledOtherSchedule = false
              for (let entry of data.entry) {
                if(!entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/nmcb-registration-application-profile")) {
                  continue
                }
                let practitioner = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
                })?.valueReference?.reference
                if('Practitioner/' + result.id !== practitioner) {
                  continue
                }
                alreadyEnrolledOtherSchedule = data.entry.find((app) => {
                  return app.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile") &&
                  app.resource.extension.find((ext) => {
                    return ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-registration-application-reference"
                  }).valueReference.reference === "Basic/" + entry.resource.id
                })
                let cadreCategory = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-cadre-category'
                })?.valueCoding
                if(cadreCategory) {
                  result.cadreCategory = cadreCategory.display
                }
                let location = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-training-location'
                })?.valueCoding
                if(location) {
                  result.location = location.display
                }
                result.application = entry.resource.id
              }
              if(alreadyEnrolledOtherSchedule) {
                continue
              }
              this.applicants.results.push(result);
            }
          }
          this.applicants.total = data.total;
          this.applicants.loadingApplicants = false;
          this.checkRerunApplicants()
        }).catch(err => {
          this.applicants.loadingApplicants = false;
          this.applicants.error_message = "Unable to load results.";
          this.checkRerunApplicants()
          console.log(err);
        });
      }).catch(err => {
        this.applicants.loadingApplicants = false;
        this.applicants.error_message = "Unable to load results.";
        this.checkRerunApplicants()
        console.log(err);
      });
    },
    getEnrolled(restart) {
      this.enrolled.selected = []
      this.enrolled.dialog = true
      if ( this.enrolled.loadingEnrolled ) {
        this.enrolled.update_again.rerun = true
        this.enrolled.update_again.restart = this.enrolled.update_again.restart || restart
        return
      }
      this.enrolled.loadingEnrolled = true;
      this.enrolled.error_message = null;
      let url = "";
      if (restart) this.enrolled.options.page = 1;
      if (this.enrolled.options.page > 1) {
        if (this.enrolled.options.page === this.enrolled.prevPage - 1) {
          url = this.enrolled.link.find(link => link.relation === "previous").url;
        } else if (this.enrolled.options.page === this.enrolled.prevPage + 1) {
          url = this.enrolled.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace(/_getpages=[^&]*&*/, "").replace("/fhir?","/fhir/Basic?")
        url = url.substring(url.indexOf("/fhir/"));

        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_profile=http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile') === -1) {
          url = url + '&_profile=http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile'
        }
        if(url.indexOf('nmcbscheduledexam=Basic/' + this.queries.exam) === -1) {
          url = url + '&nmcbscheduledexam=Basic/' + this.queries.exam
        }
        if(url.indexOf('_include=Basic:practitioner') === -1) {
          url = url + '&_include=Basic:practitioner'
        }
        if(url.indexOf('_sort=-_lastUpdated') === -1) {
          url = url + '&_sort=-_lastUpdated'
        }
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
      }
      if (url === "") {
        let count = this.enrolled.options.itemsPerPage;
        let sort = "";
        for (let idx in this.enrolled.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.enrolled.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.enrolled.options.sortBy[idx];
        }
        url =
          "/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile" + 
          "&nmcbscheduledexam=Basic/" + this.queries.exam + 
          "&_include=Basic:nmcbapplication" + 
          "&_include=Basic:practitioner" + 
          "&_count=" + count +
          "&_sort=-_lastUpdated&_total=accurate"
      }
      let sTerms = Object.keys(this.enrolled.terms);
      for (let term of sTerms) {
        if ( Array.isArray( this.enrolled.terms[term] ) ) {
          if ( this.enrolled.terms[term].length > 0 ) {
            url += "&" + term + "=" + this.enrolled.terms[term].join(',')
          }
        } else if ( this.enrolled.terms[term] ) {
          url += "&" + term + "=" + this.enrolled.terms[term];
        }
      }

      this.enrolled.prevPage = this.enrolled.options.page;
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.enrolled.results = [];
          if (data.total > 0) {
            this.enrolled.link = data.link;
            for (let entry of data.entry) {
              if(entry.resource.resourceType !== 'Practitioner') {
                continue
              }
              let result = { id: entry.resource.id };
              result.surname = entry.resource.name[0].family
              result.given = entry.resource.name[0].given.join(", ")
              result.birthDate = entry.resource.birthDate
              result.gender = entry.resource.gender
              result.gender = await this.$fhirutils.lookup( result.gender, 'http://hl7.org/fhir/administrative-gender' )
              result.omang = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='omang').value" );
              result.passport = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='passport').value" );

              for (let entry of data.entry) {
                let practitioner = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
                })?.valueReference?.reference
                if('Practitioner/' + result.id !== practitioner) {
                  continue
                }
                if(entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile")) {
                  if(!this.examResults[entry.resource.id]) {
                    this.$set(this.examResults, entry.resource.id, {})
                  }
                  result.examId = entry.resource.id
                  for(let assessment of this.exam.assessments) {
                    let score = ""
                    for(let ext of entry.resource.extension) {
                      if(ext.url === "http://ihris.org/fhir/StructureDefinition/" + assessment.code + "-score") {
                        score = ext.valueDecimal
                        break
                      }
                    }
                    if(score && !this.examResults[entry.resource.id][assessment.code]) {
                      this.$set(this.examResults[entry.resource.id], assessment.code, {score: score, errors: []})
                    } else if(!score && !this.examResults[entry.resource.id][assessment.code]) {
                      this.$set(this.examResults[entry.resource.id], assessment.code, {score: "", errors: []})
                    }
                  }
                  let assessment = entry.resource.extension.find((ext) => {
                    return ext.url === 'http://ihris.org/fhir/StructureDefinition/exam-assessment'
                  })
                  if(assessment && !this.examResults[entry.resource.id]["exam-assessment"]) {
                    this.$set(this.examResults[entry.resource.id], "exam-assessment", assessment.valueCoding.display)
                  } else if(!assessment && !this.examResults[entry.resource.id]["exam-assessment"]){
                    this.$set(this.examResults[entry.resource.id], "exam-assessment", "")
                  }
                }
                if(!entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/nmcb-registration-application-profile")) {
                  continue
                }
                let cadreCategory = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-cadre-category'
                })?.valueCoding
                if(cadreCategory) {
                  result.cadreCategory = cadreCategory.display
                }
                let location = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-training-location'
                })?.valueCoding
                if(location) {
                  result.location = location.display
                }
                result.application = entry.resource.id
              }
              this.enrolled.results.push(result);
            }
          }
          this.enrolled.total = data.total;
          this.enrolled.loadingEnrolled = false;
          this.checkRerunEnrolled()
        }).catch(err => {
          this.enrolled.loadingEnrolled = false;
          this.enrolled.error_message = "Unable to load results.";
          this.checkRerunEnrolled()
          console.log(err);
        });
      }).catch(err => {
        this.enrolled.loadingEnrolled = false;
        this.enrolled.error_message = "Unable to load results.";
        this.checkRerun()
        console.log(err);
      });
    }
    
  },
  created() {
    this.getExam()
    this.getEnrolled()
  }
}
</script>